.myModal{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: none;
    background: rgba(0, 0, 0, 0.5);
}

.myModalContent{
    padding: 25px;
    background: white;
    min-width: 250px;
    border-radius: 34px;
}

.myModal.active{
    display: flex;
    justify-content: center;
    align-items: center; 
}



