.loaderLoad{
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: 3px dashed green;
    animation: rotate 2s infinite linear;
}

@keyframes rotate{
    0%{
        transform: rotate(0deg) scale(0.5);
    }
    100%{
        transform: rotate(360deg) scale(1.5);
    }
}